<template>
  <div class="edit page">
    <nav>
      <div class="left-side">
        <span @click="onBtnBack">后退</span>
      </div>
      <div class="right-side">
        <span @click="rotateLeft">左转</span>
        <span @click="rotateRight">右转</span>
        <span @click="onFinish">完成</span>
      </div>
    </nav>    
    <div class="content">
      <vueCropper
        ref="cropper"
        :autoCrop="true"
        :fixed="true"
        :fixedNumber="[25, 36]"
        :img="img"
        :centerBox="true"
      ></vueCropper>      
    </div>
  </div>
</template>
<script>
import { VueCropper }  from 'vue-cropper' 
import { mapState } from 'vuex'
export default {
  name: 'Home',
  data() {
    return {
      img: "",
      id: null,
      name: ""
    }
  },
  mounted() {
    // console.log(this.$route)
    console.log(this.$route.params.img)
    this.img = URL.createObjectURL(this.$route.params.img);
    this.id = this.$route.params.id;
    this.name = this.removeTypeName(this.$route.params.img.name);
    this.$refs.cropper.startCrop() ;
    console.log(this.$refs.cropper)
    console.log(this.startCode)
  },
  beforeDestroy(){
    URL.revokeObjectURL(this.img);
  },
  components: {
    VueCropper,
  },  
  computed: {
    ...mapState([
      "list", "startCode"
    ])
  },  
  methods: {
    onBtnBack() {
      this.$router.back();
    },
    rotateLeft() {
      this.$refs.cropper.rotateLeft() 
    },
    rotateRight() {
      this.$refs.cropper.rotateRight()
    },
    generateId() {
      return parseInt(100000 * Math.random());
    },
    removeTypeName(fileName) {
      return fileName.replace(/\.[^\.]*$/,"");
    },    
    getItemById(id) {
      for(var i = 0; i < this.list.length; i++) {
        if(this.list[i].id == id) {
          return this.list[i]
        }
      }
      return null;
    },
    onFinish() {
      this.$refs.cropper.getCropData((data) => {
        // do something
        // console.log(data)  
        if(!this.id) {
          // let n = this.list.length;
          this.$store.commit("addItem", {
            id: this.generateId(),
            // code: this.startCode + n,
            name: this.name,
            img: data
          })
        }
        else {
          let item = this.getItemById(this.id);
          if(!item){
            return;
          }
          let o = {
            ...item,
            img: data
          }
          this.$store.commit("editItem", o);
        }
        this.$router.back();
      })
    },
  }
}
</script>
