<template>
  <div class="img">
    <nav>
      图片
      <div class="left-side">
        <span @click="$router.back()">后退</span>
      </div>
      <div class="right-side">
        <span @click="onSave">保存</span>
      </div>      
    </nav>    
    <div class="content img-content" ref="imgbox">
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'Home',
  data() {
    return {
      dlShow: false,
      img: null
    }
  },
  mounted(){
    this.img = this.$route.params.img;
    this.$refs.imgbox.appendChild(this.img)
  },
  methods: {
    onSave() {
      if(this.isWeiXin()) {
        this.$Modal.warning({
            title: "提示",
            content: "请长按图片保存"
        });
        return;
      }
      let link = document.createElement("a");
      link.setAttribute("href", this.img.src);
      link.setAttribute("download", this.formatTime() + ".jpg");
      link.click();
      link = null;
    },
    formatTime(obj, arg) {
      function fixNum(num)
      {
        num = parseInt(num);
        if(num < 10)
          return "0" + num;
        return num;  
      }
      function to12Hour(num)
      {
         num = parseInt(num);
         if(num > 12)
         {
           return num - 12; 
         } 
         return num;
      }
      if(!obj)
        obj = new Date();
      let year = obj.getFullYear();
      let month = fixNum(obj.getMonth() + 1);
      let date = fixNum(obj.getDate());
      let hour24 = fixNum(obj.getHours());
      let hour12 = fixNum(to12Hour(obj.getHours()));
      let minute = fixNum(obj.getMinutes());
      let second = fixNum(obj.getSeconds());
      if(!arg)
      {
        return "" + year + month + date + hour24 + minute + second;  
      }
      arg = arg.replace(/yyyy/g, year);
      arg = arg.replace(/MM/g, month);
      arg = arg.replace(/dd/g, date);
      arg = arg.replace(/HH/g, hour24);
      arg = arg.replace(/hh/g, hour12);
      arg = arg.replace(/mm/g, minute);
      arg = arg.replace(/ss/g, second);
      return arg;      
    },
    isWeiXin() {
      var ua = window.navigator.userAgent.toLowerCase();
      if(ua.match(/MicroMessenger/i) == 'micromessenger') {
         return true;
      } else {
         return false;
      }
    }       
  }
}
</script>
<style lang="less">
  .img-content > img {
    width:100%;
  }
</style>
